module.exports = "/************************\r\nNav CSS\r\nMalik Tillman 2019\r\n*************************/\r\n\r\n/***** Set Relational Navigation Styles *****/\r\n\r\n/* Nav */\r\n\r\n.nav, .nav-mobile {\r\n  font-family: Josefin Sans Light, serif;\r\n  letter-spacing: 3px;\r\n  font-size: 15px;\r\n}\r\n\r\n.nav-element, .nav-element-mobile {\r\n  padding: 10px 20px;\r\n  border-radius: 3px;\r\n  color: white;\r\n\r\n  text-decoration: none;\r\n\r\n  background-color: rgba(255, 255, 255, 0);\r\n  transition: background-color 1000ms ease, transform 750ms ease-in-out;\r\n  -webkit-transition: background-color 1000ms ease, transform 750ms ease-in-out;\r\n}\r\n\r\n/* Search */\r\n\r\n.search-input, .search-input-mobile {\r\n  width: 175px;\r\n\r\n  background: transparent;\r\n  border: none;\r\n  outline: none;\r\n  transition: width 1s;\r\n}\r\n\r\n#search, .search-input, .search-input-mobile, .search-bar, ::-webkit-input-placeholder {\r\n  color: white;\r\n  font-family: Josefin Sans Light, serif;\r\n  font-size: 15px;\r\n  letter-spacing: 3px;\r\n}\r\n\r\n#search, .search-input, .search-input-mobile, .search-bar, ::-moz-placeholder {\r\n  color: white;\r\n  font-family: Josefin Sans Light, serif;\r\n  font-size: 15px;\r\n  letter-spacing: 3px;\r\n}\r\n\r\n#search, .search-input, .search-input-mobile, .search-bar, ::-ms-input-placeholder {\r\n  color: white;\r\n  font-family: Josefin Sans Light, serif;\r\n  font-size: 15px;\r\n  letter-spacing: 3px;\r\n}\r\n\r\n#search, .search-input, .search-input-mobile, .search-bar, ::placeholder {\r\n  color: white;\r\n  font-family: Josefin Sans Light, serif;\r\n  font-size: 15px;\r\n  letter-spacing: 3px;\r\n}\r\n\r\n.search-bar {\r\n  padding: 5px 10px;\r\n  border-style: solid;\r\n  border-width: 1px;\r\n\r\n  transition: background-color 1s ease-out;\r\n}\r\n\r\n.search-bar:hover {\r\n  background-color: rgba(41, 125, 48, 0.2);\r\n  transition: background-color .2s ease-in;\r\n  -webkit-transition: background-color .2s ease-in;\r\n}\r\n\r\n/***** Setup Mobile Navigation Styles *****/\r\n\r\n/* Mobile Nav */\r\n\r\n.nav-container-mobile {\r\n  position: absolute;\r\n  height: 100vh;\r\n  width: 100vw;\r\n\r\n  z-index: 1;\r\n\r\n  background-color: white;\r\n\r\n  display: block;\r\n}\r\n\r\n.nav-mobile {\r\n  font-size: 25px !important;\r\n}\r\n\r\n.nav-element-mobile {\r\n  display: inline;\r\n\r\n  color: black !important;\r\n  padding: 5px 0 !important;\r\n}\r\n\r\n.nav-element-mobile:hover {\r\n  transform: translate(10px, 5px) !important;\r\n}\r\n\r\n/* Mobile Search */\r\n\r\n.search-bar-mobile, .search-input-mobile, .search-input-mobile::-webkit-input-placeholder {\r\n  color: black !important;\r\n}\r\n\r\n.search-bar-mobile, .search-input-mobile, .search-input-mobile::-moz-placeholder {\r\n  color: black !important;\r\n}\r\n\r\n.search-bar-mobile, .search-input-mobile, .search-input-mobile::-ms-input-placeholder {\r\n  color: black !important;\r\n}\r\n\r\n.search-bar-mobile, .search-input-mobile, .search-input-mobile::placeholder {\r\n  color: black !important;\r\n}\r\n\r\n.search-bar-mobile {\r\n  box-shadow: 0 5px 10px rgba(0, 0, 0, .15);\r\n}\r\n\r\n.nav-element-mobile, .search-container-mobile, .nav-header {\r\n  opacity: 0;\r\n}\r\n\r\n/* Menu Button */\r\n\r\n.hamburger-container {\r\n  padding: 2vh;\r\n  z-index: 2;\r\n\r\n  width: 8vh;\r\n\r\n  background-color: rgba(255, 255, 255, 1);\r\n\r\n  transition: width 1000ms;\r\n}\r\n\r\n.grow {\r\n  width: 100vw;\r\n}\r\n\r\n.bar {\r\n  width: 25px;\r\n  height: 2px;\r\n  background-color: black;\r\n  margin-bottom: 4px;\r\n}\r\n\r\n/* Logo SVG Styles */\r\n\r\n#dist {\r\n  fill: #000000;\r\n}\r\n\r\n/* Logo Container Styles */\r\n\r\n.logo {\r\n  width: 8vw;\r\n  height: 8vw;\r\n}\r\n\r\n/* Name and Tag Line Styles */\r\n\r\n#name {\r\n  padding-left: 2vw;\r\n\r\n  font-family: Josefin Sans Light, serif;\r\n  font-size: 5vw;\r\n  letter-spacing: 10px;\r\n  color: black;\r\n}\r\n\r\n.tagLine {\r\n  font-family: Josefin Sans Thin, serif;\r\n  font-size: 2vw;\r\n  letter-spacing: 5px;\r\n  color: black;\r\n}\r\n\r\n@media (max-width: 400px) {\r\n  #name {\r\n    letter-spacing: 5px;\r\n  }\r\n}\r\n\r\n@media (max-width: 250px) {\r\n  .logo, #name {\r\n    display: none;\r\n  }\r\n\r\n}\r\n\r\n/***** Style Desktop Navigation *****/\r\n\r\n.search-input::-webkit-input-placeholder {\r\n  opacity: .6;\r\n}\r\n\r\n.search-input::-moz-placeholder {\r\n  opacity: .6;\r\n}\r\n\r\n.search-input::-ms-input-placeholder {\r\n  opacity: .6;\r\n}\r\n\r\n.search-input::placeholder {\r\n  opacity: .6;\r\n}\r\n\r\n.nav-element:hover {\r\n  background-color: rgba(255, 255, 255, .2);\r\n  transform: translate(0, -10px) !important;\r\n}\r\n\r\n/* Search Icon */\r\n\r\n.search-icon {\r\n  margin-left: 15px;\r\n  margin-right: 15px;\r\n  width: 30px;\r\n  padding: 5px 5px;\r\n  border-radius: 5px;\r\n}\r\n\r\n.search-icon:hover {\r\n  background-color: rgba(41, 125, 48, 0.2);\r\n}\r\n\r\n@media (max-width: 1366px) {\r\n  .nav-element {\r\n    padding: 5px 10px;\r\n  }\r\n}\r\n\r\n@media (max-width: 1199px) {\r\n  .nav-element {\r\n    padding: 10px 20px;\r\n  }\r\n}\r\n\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"